<template>
  <!-- 密码修改 -->
  <div class="c_lyt_main">
    <h3 class="lm_title">密码修改</h3>
    <div class="lyt_uc_form">
      <div class="ucf_title">密码修改</div>
      <div class="ucf_main">
        <div class="ucf_form">
          <form @submit.prevent="modifyPwdSubmit">
            <p class="fm_item">
              <label>旧密码：</label>
              <input type="text" name="oldpwd" v-model="oldpwd" v-validate="'required|numeric'">
              <span v-show="errors.has('oldpwd')" class="help is-danger">{{ errors.first('oldpwd')}}</span>
            </p>
            <p class="fm_item">
              <label>新密码：</label>
              <input type="text" name="newpwd" v-model="newpwd" v-validate="'required'">
              <span v-show="errors.has('newpwd')" class="help is-danger">{{ errors.first('newpwd') }}</span>
            </p>
            <p class="fm_item">
              <label>再次输入新密码：</label>
              <input type="text" name="renewpwd" v-model="renewpwd" v-validate="'required'">
              <span v-show="errors.has('renewpwd')" class="help is-danger">{{ errors.first('renewpwd') }}</span>
            </p>
            <p class="fm_btns">
              <button type="submit" class="btn_save">确认</button>
            </p>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data(){
      return {
        oldpwd: '',
        newpwd: '',
        renewpwd: ''
      }
    },
    methods: {
      /* 修改密码 */
      modifyPwdSubmit(){
        this.$validator.validateAll().then(() => {
          console.log('From Submitted!');
        }).catch(() => {
          console.log('Correct them errors!');
        });
      }
    }
  }
</script>
<style lang="scss">
  @import "../../styles/common/lyt_uc_main.css";
  @import "../../styles/common/lyt_uc_form.css";
</style>



