<template>
  <!-- 登录和注册 -->
  <div class="c_regist lyt_login_regist">
    <div class="lr_bg"></div>
    <div class="lr_main">
      <div class="lr_cont">
        <h1 class="lr_logo"><img src="../../assets/logo.png"></h1>
        <form @submit.prevent="loginSubmit" class="lyt_form">
          <div class="fm_item">
            <input type="text" name="username" v-model="username" v-validate="'required|username'" placeholder="账号">
            <div class="fm_errs">
              <span v-show="errors.has('username')" class="help is-danger">{{ errors.first('username') }}</span>
            </div>
          </div>
          <div class="fm_item">
            <input type="text" name="password" v-model="password" v-validate="'required|min:6|alpha_num'" placeholder="密码">
            <div class="fm_errs">
              <span v-show="errors.has('password')" class="help is-danger">{{ errors.first('password') }}</span>
            </div>
          </div>
          <div class="fm_item">
            <input type="text" name="paypwd" v-model="paypwd" v-validate="'required|min:6|alpha_num'" placeholder="二级密码">
            <div class="fm_errs">
              <span v-show="errors.has('paypwd')" class="help is-danger">{{ errors.first('paypwd') }}</span>
            </div>
          </div>
          <div class="fm_item">
            <input type="text" name="realname" v-model="realname" v-validate="'required|username'" placeholder="真实姓名">
            <div class="fm_errs">
              <span v-show="errors.has('realname')" class="help is-danger">{{ errors.first('realname') }}</span>
            </div>
          </div>
          <div class="fm_item">
            <input type="text" name="idcard" v-model="idcard" v-validate="'required|idcard'" placeholder="身份证">
            <div class="fm_errs">
              <span v-show="errors.has('idcard')" class="help is-danger">{{ errors.first('idcard') }}</span>
            </div>
          </div>
          <div class="fm_item">
            <input type="text" name="recoman" v-model="recoman" v-validate="'required|username'" placeholder="推荐人">
            <div class="fm_errs">
              <span v-show="errors.has('recoman')" class="help is-danger">{{ errors.first('recoman') }}</span>
            </div>
          </div>
          <div class="fm_item">
            <input type="text" name="phone" v-model="phone" v-validate="'required|phone'" placeholder="手机号">
            <div class="fm_errs">
              <span v-show="errors.has('phone')" class="help is-danger">{{ errors.first('phone') }}</span>
            </div>
          </div>
          <div class="fm_item">
            <input type="text" name="address" v-model="address" v-validate="'required'" @focus="choiceAreas" placeholder="地址">
            <div class="fm_errs">
              <span v-show="errors.has('address')" class="help is-danger">请选择地址</span>
            </div>
          </div>
          <div class="fm_item half">
            <input type="text" name="verifycode" v-model="verifycode" v-validate="'required'" placeholder="验证码">
            <div class="fm_verifycode"><img src="../../assets/verify_code.jpg"></div>
            <div class="fm_errs">
              <span v-show="errors.has('verifycode')" class="help is-danger">{{ errors.first('verifycode') }}</span>
            </div>
          </div>
          <div class="fm_btns">
            <button type="submit">注册</button>
          </div>
          <div class="fm_bom_link"><a href="#/login" class="first">登录</a></div>
        </form>
      </div>
    </div>
    <areas :isShow="isShowAreas" @onAreasOk="onAreasOk" @onAreasCancel="onAreasCancel"></areas>
  </div>
</template>
<script>
  import Areas from '../../components/areas.vue'
  export default {
    data(){
      return {
        isShowAreas: false,
        username: '',
        password: '',
        paypwd: '',
        realname: '',
        idcard: '',
        recoman: '',
        phone: '',
        verifycode: '',
        address:''
      }
    },
    methods: {
      /* 登录表单提交函数 */
      loginSubmit(){
        this.$validator.validateAll().then(() => {
          console.log('From Submitted!');
        }).catch(() => {
          console.log('Correct them errors!');
        });
      },
      choiceAreas(){
        this.isShowAreas = true;
      },
      onAreasOk(result){
        this.address = result.name;
        this.isShowAreas = false;
        this.$validator.errorBag.remove('address')
      },
      onAreasCancel(result){
          this.isShowAreas = result;
      }
    },
    components: {
        Areas
    }
  }
</script>
<style lang="scss">
  @import "../../styles/login/lyt_login_regist.css";

  .c_regist .lr_cont {
    padding-bottom: 50px !important;
  }
</style>



