<template>
  <!-- 购物车 -->
  <div class="c_lyt_main">
    <h3 class="lm_title">购物车</h3>
    <div class="lyt_table lyt_order lyt_cart">
      <!-- 一个订单 -->
      <table>
        <thead>
        <tr>
          <th width="40%"><input type="checkbox" class="chkbox" v-model="checkedAllModel" @change="checkedAll"></th>
          <th width="15%">规格</th>
          <th width="15%">价格</th>
          <th width="10%">积分</th>
          <th width="10%">数量</th>
          <th width="10%">操作</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(item,index) in productList">
          <td class="odr_detail">
            <input type="checkbox" class="chkbox chk_sub" :value="item.id" v-model="checkedProduct">
            <img src="../../assets/icon_layout.png" class="pd_img">
            <span><i>{{item.descr}}</i></span>
          </td>
          <td>{{item.spec}}</td>
          <td><i class="clr_rd">{{item.price}}</i></td>
          <td>{{item.credit}}</td>
          <td>
            <div class="set_number">
              <span class="del" :class="{disable:parseInt(item.number)==1}" @click="minus(item)">-</span>
              <input type="text" name="number" v-on:input="changeNumber(item)" v-model="item.number">
              <span class="add" @click="increase(item)">+</span>
            </div>
            <p><span v-show="errors.has('number')" class="help is-danger" style="font-size: 12px;">{{ errors.first('number') }}</span></p>
          </td>
          <td><a href="#" class="clr_rd">删除</a></td>
        </tr>
        <tr class="cart_bom">
          <td colspan="2" class="cart_bom_lt">
            <a href="#" class="go_on">继续购物</a>
            <a href="#" class="ey_cr">清空购物车</a>
          </td>
          <td colspan="4" class="cart_bom_rt">
            <p>
              <span>可获积分：<i class="clr_rd">2000</i></span>
              <span>总共支付：<i class="clr_rd">￥2000</i></span>
            </p>
            <a href="#/order_detail" class="go_stlt">结算</a>
          </td>
        </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>
<script>
  export default {
    data(){
      return {
        checkedAllModel: false,
        checkedProduct: [],
        productList: [
          {
            id: '1',
            url: '#',
            descr: '潮宏基 黄金足金 哆啦A梦 铃铛 黄金吊坠足金吊坠 女款女士吊坠 定价 约1.3克小铃铛 不含链',
            spec: '黑色 100CM',
            price: '298.00',
            credit: '2000',
            number: '100'
          },
          {
            id: '2',
            url: '#',
            descr: '潮宏基 黄金足金 哆啦A梦 铃铛 黄金吊坠足金吊坠 女款女士吊坠 定价 约1.3克小铃铛 不含链',
            spec: '黑色 100CM',
            price: '2981.00',
            credit: '2100',
            number: '160'
          },
          {
            id: '3',
            url: '#',
            descr: '潮宏基 黄金足金 哆啦A梦 铃铛 黄金吊坠足金吊坠 女款女士吊坠 定价 约1.3克小铃铛 不含链',
            spec: '黑色 100CM',
            price: '2938.00',
            credit: '400',
            number: '110'
          },
          {
            id: '4',
            url: '#',
            descr: '潮宏基 黄金足金 哆啦A梦 铃铛 黄金吊坠足金吊坠 女款女士吊坠 定价 约1.3克小铃铛 不含链',
            spec: '黑色 100CM',
            price: '228.00',
            credit: '5000',
            number: '300'
          },
          {
            id: '5',
            url: '#',
            descr: '潮宏基 黄金足金 哆啦A梦 铃铛 黄金吊坠足金吊坠 女款女士吊坠 定价 约1.3克小铃铛 不含链',
            spec: '黑色 100CM',
            price: '18.00',
            credit: '3000',
            number: '3'
          }
        ]
      }
    },
    methods: {
      checkedAll(){
        let _this = this;
        if (this.checkedAllModel) {
          _this.checkedProduct = [];
          this.productList.forEach(function (item) {
            _this.checkedProduct.push(item.id);
          })
        } else {
          this.checkedProduct = [];
        }
      },
      increase(item){
        item.number = parseInt(item.number) + 1;
      },
      minus(item){
        item.number = parseInt(item.number);
        if (item.number > 1) {
          item.number = item.number - 1;
        }
      },
      changeNumber(item){
        item.number = parseInt(item.number);
        if(isNaN(item.number)){
          item.number = 1;
        }
      }
    },
    watch: {
      'checkedProduct': {
        handler: function (val, oldVal) {
          if (this.checkedProduct.length === this.productList.length) {
            this.checkedAllModel = true;
          } else {
            this.checkedAllModel = false;
          }
        },
        deep: true
      }
    }
  }
</script>
<style lang="scss">
  @import "../../styles/common/lyt_uc_main.css";
  @import "../../styles/common/lyt_table.css";
  @import "../../styles/common/lyt_table_order.css";

  .lyt_cart .chkbox {
    float: left;
    margin: 0 15px;
  }

  .lyt_cart .chk_sub {
    margin-top: 43px;
  }

  .lyt_cart .cart_bom {
    background-color: #f6f5fa;
  }

  .lyt_cart .cart_bom_lt {
    text-align: left;
  }

  .lyt_cart .cart_bom_lt a {
    display: block;
    padding: 8px 25px;
    float: left;
    margin-left: 15px;
  }

  .lyt_cart .set_number {
    width: 99px;
    border: 1px solid #e5e5e5;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }

  .lyt_cart .set_number {
    height: 25px;
    line-height: 25px;
    overflow: hidden;
  }

  .lyt_cart .set_number span,
  .lyt_cart .set_number input {
    display: block;
    float: left;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }

  .lyt_cart .set_number span {
    width: 25px;
    height: 25px;
    cursor: pointer;
  }

  .lyt_cart .set_number input {
    width: 40px;
    height: 25px;
    line-height: 25px;
    text-align: center;
    border: none;
  }

  .lyt_cart .set_number .add {
    border-left: 1px solid #e5e5e5;
  }

  .lyt_cart .set_number .del {
    border-right: 1px solid #e5e5e5;
  }

  .lyt_cart .del.disable {
    color: #e5e5e5;
  }

  .lyt_cart .go_on {
    color: #333;
    border: 1px solid #e5e5e5;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    background-color: #fff;
  }

  .lyt_cart .ey_cr {
    color: #999;
  }

  .lyt_cart .cart_bom_rt {
    text-align: right;
    padding-right: 30px;
  }

  .lyt_cart .cart_bom_rt p span {
    margin-left: 20px;
    color: #999;
  }

  .lyt_cart .go_stlt {
    display: block;
    padding: 8px 40px;
    color: #fff;
    float: right;
    margin-top: 20px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    background-color: #6f0afe;
  }
</style>



