<template>
  <!-- 意见反馈 -->
  <div class="c_lyt_main">
    <h3 class="lm_title">意见反馈</h3>
    <div class="lyt_uc_form">
      <div class="ucf_title">意见反馈</div>
      <div class="ucf_main">
        <div class="ucf_tips">
         请您对我们提出宝贵意见和建议,您留下的每个意见都是对我们产品不断改进的动力!
        </div>
        <div class="ucf_form lyt_feedback">
          <form @submit.prevent="feedBackSubmit">
            <p class="fm_item">
              <label>意见建议：</label>
              <textarea name="suggest" v-model="suggest" v-validate="'required'" cols="30" rows="10"></textarea>
              <span v-show="errors.has('suggest')" class="help is-danger">{{ errors.first('suggest')}}</span>
            </p>
            <p class="fm_item">
              <label>您的 QQ：</label>
              <input type="text" name="qq" v-model="qq" v-validate="'required'">
              <i class="tips">我们可能邀请您加入产品讨论群,共同参与产品建设.</i>
              <span v-show="errors.has('qq')" class="help is-danger">{{ errors.first('qq') }}</span>
            </p>
            <p class="fm_item">
              <label>您的邮箱：</label>
              <input type="text" name="email" v-model="email" v-validate="'required|email'">
              <i class="tips">留下真实邮箱,您将有机会获得我们送出的礼品.</i>
              <span v-show="errors.has('email')" class="help is-danger">{{ errors.first('email') }}</span>
            </p>
            <p class="fm_item">
              <label>图片上传：</label>
              <vue-core-image-upload
                :crop="false"
                @imageuploaded="imageuploaded"
                :max-file-size="5242880"
                url="http://101.198.151.190/api/upload.php" >
                <img :src="picture" width="91" height="91">
              </vue-core-image-upload>
              <span class="help upld_tips">请上传本地图片,大小不超过2M,格式为JPG,PNG</span>
            </p>
            <p class="fm_btns">
              <button type="submit" class="btn_save">确认</button>
            </p>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import VueCoreImageUpload from 'vue-core-image-upload'
  import plus from '../../assets/upload_photos.png'
  export default {
    data(){
      return {
        suggest: '',
        qq: '',
        email: '',
        picture:plus
      }
    },
    methods: {
      /* 修改密码 */
      feedBackSubmit(){
        this.$validator.validateAll().then(() => {
          console.log('From Submitted!');
        }).catch(() => {
          console.log('Correct them errors!');
        });
      },
      imageuploaded(res){
        this.picture = res.data.src;
      }
    },
    components:{
      VueCoreImageUpload
    }
  }
</script>
<style lang="scss">
  @import "../../styles/common/lyt_uc_main.css";
  @import "../../styles/common/lyt_uc_form.css";
  .lyt_feedback {
    padding-bottom: 100px;
  }
  .lyt_feedback .fm_item {
    position: relative;
  }
  .lyt_feedback .fm_item .help{
    position: absolute;
    left: 125px;
    bottom: -18px;
  }
  .lyt_feedback .tips {
    display: block;
    float: left;
    color: #999;
    font-size: 14px;
    margin: 10px 0 0 10px;
  }
  .lyt_feedback .upld_tips{
    color: #999;
    font-size: 14px;
  }
  .lyt_feedback .fm_btns{
    margin-top: 40px;
  }
</style>



