<template>
  <!-- 我的充值 -->
  <div class="c_lyt_main">
    <h3 class="lm_title">我的充值</h3>
    <div class="lyt_uc_form">
      <div class="ucf_title">账户充值</div>
      <div class="ucf_main lyt_deposit">
        <dl class="dpt_sum">
          <dt>充值金额：</dt>
          <dd class="clearfix">
            <span v-for="(item,index) in sumList" :class="{default:index == sumDef}" @click="choiceSum(index)">{{item}}</span>
            <span @click="choiceOtherSum">其他金额</span>
          </dd>
        </dl>
        <dl>
          <dt>支付方式：</dt>
          <dd class="pay_way">
            <span :class="{default:0==payWay}" @click="payWay=0">支付宝</span>
            <span :class="{default:1==payWay}" @click="payWay=1">网银支付</span>
          </dd>
          <dd v-show="payWay==1">
            银行列表
          </dd>
        </dl>
        <dl>
          <dt>选择卡种：</dt>
          <dd>
              <label class="lyt_radio"><input type="radio" name="card_type" v-model="cardType" value="0">储蓄卡</label>
              <label class="lyt_radio"><input type="radio" name="card_type" v-model="cardType" value="1">信用卡</label>
          </dd>
        </dl>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data(){
      return {
        sumList: ['￥100', '￥500', '￥1000', '￥10000', '￥1000000', '￥1000000'],
        sumDef:0,
        payWay:0,
        cardType:0
      }
    },
    methods: {
      choiceSum(index){
          this.sumDef = index;
      },
      choiceOtherSum(){
          alert('1');
      }
    }
  }
</script>
<style lang="scss">
  @import "../../styles/common/lyt_uc_main.css";
  @import "../../styles/common/lyt_uc_form.css";
  .lyt_deposit .dpt_sum{
    overflow: hidden;
  }
  .lyt_deposit dl dt{
    padding: 20px 0 10px;
  }
  .lyt_deposit .dpt_sum dd{
    width: 560px;
  }
  .lyt_deposit .dpt_sum span {
    display: block;
    width: 118px;
    height: 48px;
    line-height: 48px;
    text-align: center;
    float: left;
    cursor: pointer;
    margin: 0 20px 20px 0;
    border: 1px solid #dddddd;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
  .lyt_deposit .default{
    position: relative;
    border-color: #700aff !important;
  }
  .lyt_deposit .default:after{
    position: absolute;
    top: 0;
    right: 0;
    display: block;
    width: 30px;
    height: 30px;
    content: "";
    background: url("../../assets/icon_layout.png");
    background-position: -104px -116px;
  }
  .lyt_deposit .pay_way{
    height: 45px;
  }
  .lyt_deposit .pay_way span{
    display: block;
    width: 158px;
    height: 43px;
    line-height: 43px;
    text-align: center;
    float: left;
    margin-right: 20px;
    cursor: pointer;
    border: 1px solid #dddddd;
  }
  .lyt_radio {
    font-size: 14px;
    margin-right: 20px;
  }
  .lyt_radio input {
    position: relative;
    top: 2px;
    margin-right: 5px;
  }
</style>



