<template>
  <!-- 商品详情 -->
  <div class="c_lyt_main">
    <h3 class="lm_title">商品详情</h3>
    <!-- Product-Detail-Content -->
    <div class="lyt_pdt_dtl">
      <!-- 详情上面的 -->
      <div class="pd_top">
        <!-- 左边 -->
        <div class="pd_top_lt">
          <div class="pd_picture"><img src="../../assets/ipad.jpg"></div>
        </div>
        <!-- 右边 -->
        <div class="pd_top_rt">
          <div class="pd_top_tit">{{title}}</div>
          <div class="item pd_price">
            <label>价　　格：</label><span class="clr_rd">￥{{price}}</span>
          </div>
          <div class="item pd_credit">
            <label>获得积分：</label><span class="clr_rd">{{credit}}</span>
          </div>
          <div class="item pd_color">
            <label>颜　　色：</label>
            <span class="im_box">
              <i v-for="(item,index) in color" :class="{active:index==colorId}" @click="toggleColor(index)">{{item}}</i>
            </span>
          </div>
          <div class="item pd_size">
            <label>尺　　码：</label>
            <span class="im_box">
              <i v-for="(item,index) in size" :class="{active:index==sizeId}" @click="toggleSize(index)">{{item}}</i>
            </span>
          </div>
          <div class="item pd_number">
            <label>数　　量：</label>
            <span class="im_choice">
              <i @click="minus" :class="{disable:parseInt(number)==1}">-</i>
              <input type="text" name="number" v-on:input="changeNumber()" v-model="number">
              <i @click="increase">+</i>
            </span>
          </div>
          <a href="#" class="pd_btn">加入购物车</a>
        </div>
      </div>

      <!-- 详情下面的 -->
      <div class="pd_bom">
        <h3 class="pd_bom_tit">商品详情</h3>
        <div class="pd_bom_main">
          <div class="pd_bm_text">
            <ul class="clearfix">
              <li>商品名称：Ipad Mini</li>
              <li>商品编号：333339999</li>
              <li>商品毛重：2.68kg</li>
              <li>商品产地：中国大陆</li>
              <li>系统：其他</li>
              <li>厚度：10.00mm-15.00mm</li>
              <li>内存容量：8G</li>
              <li>分辨率：超高清屏</li>
              <li>商品名称：Ipad Mini</li>
              <li>商品编号：333339999</li>
              <li>商品毛重：2.68kg</li>
              <li>商品产地：中国大陆</li>
              <li>系统：其他</li>
              <li>厚度：10.00mm-15.00mm</li>
              <li>内存容量：8G</li>
              <li>分辨率：超高清屏</li>
            </ul>
          </div>
          <div class="pd_bom_img">
            <img src="../../assets/product_detail.jpg" alt="">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data(){
      return {
        title: "Apple iPad mini 4 平板电脑 7.9英寸（128G WLAN版/A8芯片/Retina显示屏/Touch ID技术 MK9P2CH）银色",
        price: 1990,
        credit: 2000,
        color: ['黑色', '红色', '白色'],
        size: ['50CM', '100CM', '150CM'],
        number: 1,
        colorId: 0,
        sizeId: 0
      }
    },
    methods: {
      increase(){
        this.number = parseInt(this.number) + 1;
      },
      minus(){
        this.number = parseInt(this.number);
        if (this.number > 1) {
          this.number = this.number - 1;
        }
      },
      changeNumber(){
        this.number = parseInt(this.number);
        if (isNaN(this.number)) {
          this.number = 1;
        }
      },
      toggleColor(index){
        this.colorId = index;
      },
      toggleSize(index){
        this.sizeId = index;
      }
    }
  }
</script>
<style lang="scss">
  @import "../../styles/common/lyt_uc_main.css";

  .lyt_pdt_dtl {
    padding: 30px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    background-color: #fff;
  }

  .lyt_pdt_dtl .pd_top {
    width: 100%;
    height: 430px;
  }

  .lyt_pdt_dtl .pd_top_lt {
    display: table;
    width: 450px;
    height: 400px;
    float: left;
    /*    margin-right: 30px;*/
    background-color: #fff;
    border: 1px solid #e5e5e5;
  }

  .lyt_pdt_dtl .pd_picture {
    display: table-cell;
    vertical-align: middle;
  }

  .lyt_pdt_dtl .pd_picture img {
    display: block;
    max-height: 400px;
    margin: 0 auto;
  }

  .lyt_pdt_dtl .pd_top_rt {
    width: 50%;
    float: left;
    padding: 10px 30px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }

  .lyt_pdt_dtl .pd_top_tit {
    max-height: 60px;
    line-height: 20px;
    margin-bottom: 20px;
    overflow: hidden;
  }

  .lyt_pdt_dtl .pd_top_rt .item {
    width: 100%;
    height: 35px;
    line-height: 35px;
  }

  .lyt_pdt_dtl .item label {
    display: block;
    width: 85px;
    float: left;
    font-size: 14px;
    padding-left: 10px;
  }

  .lyt_pdt_dtl .item span {
    display: block;
    float: left;
  }

  .lyt_pdt_dtl .pd_price {
    background-color: #f7f7f7;
  }

  .lyt_pdt_dtl .pd_price .clr_rd {
    font-weight: 600;
  }

  .lyt_pdt_dtl .pd_credit {
    margin: 8px 0;
  }

  .lyt_pdt_dtl .pd_color,
  .lyt_pdt_dtl .pd_size {
    margin-bottom: 15px;
  }

  .lyt_pdt_dtl .im_box i {
    display: block;
    line-height: normal;
    padding: 7px 15px;
    float: left;
    font-size: 14px;
    margin-right: 15px;
    cursor: pointer;
    border: 1px solid #e5e5e5;
  }

  .lyt_pdt_dtl .im_box i.active {
    border-color: #000;
  }

  .lyt_pdt_dtl .im_choice {
    width: 120px;
    height: 33px;
    border: 1px solid #e5e5e5;
  }

  .lyt_pdt_dtl .im_choice i,
  .lyt_pdt_dtl .im_choice input {
    display: block;
    float: left;
    height: 33px;
    line-height: 33px;
    text-align: center;
  }

  .lyt_pdt_dtl .im_choice i {
    width: 35px;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-color: #f6f5fa;
  }

  .lyt_pdt_dtl .im_choice i.disable {
    color: #e0e0e0;
  }

  .lyt_pdt_dtl .im_choice input {
    width: 50px;
    border: none;
    border-right: 1px solid #e5e5e5;
    border-left: 1px solid #e5e5e5;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }

  .lyt_pdt_dtl .pd_btn {
    display: block;
    width: 150px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    margin: 15px 0 0 10px;
    color: #fff;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
    background-color: #ff3300;
  }

  /* 详情下面 */
  .lyt_pdt_dtl .pd_bom {
    border: 1px solid #e5e5e5;
  }

  .lyt_pdt_dtl .pd_bom_tit {
    height: 50px;
    line-height: 50px;
    text-indent: 20px;
    background-color: #f6f8f7;
  }
  .lyt_pdt_dtl .pd_bm_text {
    padding: 20px 15px;
    overflow: hidden;
  }
  .lyt_pdt_dtl .pd_bm_text ul {
    padding-bottom: 20px;
    border-bottom: 1px solid #e5e5e5;
  }
  .lyt_pdt_dtl .pd_bm_text ul li{
    width: 25%;
    float: left;
    font-size: 14px;
    padding: 5px 0;
  }
  .lyt_pdt_dtl .pd_bom_img img{
    display: block;
    margin: 10px auto;
  }
</style>



