import Vue from 'vue'
import Router from 'vue-router'
import NotFound from '@/components/404.vue'
import Login from '@/views/login/login'
import Regist from '@/views/regist/regist'
import Forget from '@/views/forget/forget'
import Layout from '@/views/layout/layout'
import UserCenter from '@/views/layout/user_center'
import MyWallet from '@/views/layout/my_wallet'
import MyReward from '@/views/layout/my_reward'
import TransferAccount from '@/views/layout/transfer_account'
import MyCommend from '@/views/layout/my_commend'
import ApplyWithdraw from '@/views/layout/apply_withdraw'
import MyWithdraw from '@/views/layout/my_withdraw'
import MyOrders from '@/views/layout/my_orders'
import BuyProducts from '@/views/layout/buy_products'
import ProductDetail from '@/views/layout/product_detail'
import MyCart from '@/views/layout/my_cart'
import OrderDetail from '@/views/layout/order_detail'
import PaySuccess from '@/views/layout/pay_success'
import ConsigneeAddress from '@/views/layout/consignee_address'
import MyInfo from '@/views/layout/my_info'
import ModifyPwd from '@/views/layout/modify_pwd'
import ModifySecpwd from '@/views/layout/modify_secpwd'
import FeedBack from '@/views/layout/feedback'
import Deposit from '@/views/layout/deposit'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '*',
      name: 'NotFound',
      component: NotFound
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/regist',
      name: 'Regist',
      component: Regist
    },
    {
      path: '/forget',
      name: 'Forget',
      component: Forget
    },
    {
      path: '/',
      name: 'Layout',
      component: Layout,
      meta: {isLogin: true},
      children: [
        { path: '/user_center', component: UserCenter, name: 'UserCenter' },
        { path: '/my_wallet', component: MyWallet, name: 'MyWallet' },
        { path: '/my_reward', component: MyReward, name: 'MyReward' },
        { path: '/transfer_account', component: TransferAccount, name: 'TransferAccount' },
        { path: '/my_commend', component: MyCommend, name: 'MyCommend' },
        { path: '/apply_withdraw', component: ApplyWithdraw, name: 'ApplyWithdraw' },
        { path: '/my_withdraw', component: MyWithdraw, name: 'MyWithdraw' },
        { path: '/my_orders', component: MyOrders, name: 'MyOrders' },
        { path: '/buy_products', component: BuyProducts, name: 'BuyProducts' },
        { path: '/product_detail', component: ProductDetail, name: 'ProductDetail' },
        { path: '/my_cart', component: MyCart, name: 'MyCart' },
        { path: '/order_detail', component: OrderDetail, name: 'OrderDetail' },
        { path: '/pay_success', component: PaySuccess, name: 'PaySuccess' },
        { path: '/consignee_address', component: ConsigneeAddress, name: 'ConsigneeAddress' },
        { path: '/my_info', component: MyInfo, name: 'MyInfo' },
        { path: '/modify_pwd', component: ModifyPwd, name: 'ModifyPwd' },
        { path: '/modify_secpwd', component: ModifySecpwd, name: 'ModifySecpwd' },
        { path: '/feed_back', component: FeedBack, name: 'FeedBack' },
        { path: '/deposit', component: Deposit, name: 'Deposit' }
      ]
    }
  ]
});

export default router;



