<template>
  <!-- 地址选择 -->
  <div class="c_areas" v-show="isShow">
    <div class="ar_main">
      <div class="ar_top">选择结果：{{prov}}&nbsp;{{city}}&nbsp;{{distr}}</div>
      <div class="ar_bom">
        <div class="item">
          <div class="item_sub">
            <span v-for="item in provArr" @click="selectProv(item.code,item.name)">{{item.name}}</span>
          </div>
        </div>
        <div class="item">
          <div class="item_sub">
            <span v-for="item in cityArr" @click="selectCity(item.code,item.name)">{{item.name}}</span>
          </div>
        </div>
        <div class="item">
          <div class="item_sub">
            <span v-for="item in districtArr" @click="selectDistrict(item.code,item.name)">{{item.name}}</span>
          </div>
        </div>
      </div>
      <div class="ar_btn">
        <span class="ar_btn_ok" @click="onAreasOk">确定</span>
        <span class="ar_btn_ok" @click="onAreasCancel">取消</span>
      </div>
    </div>
  </div>
</template>
<script>
  import area from '../utils/areas'
  export default {
    props: ['isShow'],
    data(){
      return {
        provArr: [],
        cityArr: [],
        districtArr: [],
        prov: '',
        city: '',
        distr: '',
        result: {
          code: '',
          name: ''
        }
      }
    },
    mounted(){
      this.provArr = area.children(0);
    },
    methods: {
      selectProv(code, name){
        this.cityArr = area.childrenOption(code);
        this.prov = name;
        this.city = '';
        this.districtArr = '';
        /* 存值 */
        this.result.code = code;
      },
      selectCity(code, name){
        this.districtArr = area.childrenOption(code);
        this.city = name;
        this.distr = '';
        /* 存值 */
        this.result.code = this.result.code + ',' + code;
      },
      selectDistrict(code, name){
        this.distr = name;
        /* 存值 */
        this.result.code = this.result.code + ',' + code;
      },
      onAreasOk(){
        this.result.name = this.prov + ',' + this.city + ',' + this.distr;
        this.$emit('onAreasOk', this.result);
        this.onEmpty();
      },
      onAreasCancel(){
        this.$emit('onAreasCancel',false);
        this.onEmpty();
      },
      onEmpty(){
        this.cityArr = [];
        this.districtArr = [];
        this.prov = '';
        this.city = '';
        this.distr = '';
      }
    }
  }
</script>
<style>
  .c_areas {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10000;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5)
  }

  .c_areas .ar_main {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 100;
    width: 480px;
    height: 360px;
    margin: -180px 0 0 -240px;
    padding: 20px;
    background-color: #fff;
    border-radius: 5px;
  }

  .c_areas .ar_main .ar_top {
    padding-bottom: 10px;
  }

  .c_areas .ar_main .ar_bom {
    height: 280px;
    border-top: 1px solid #e0e0e0;
    border-bottom: 1px solid #e0e0e0;
  }

  .c_areas .ar_bom .item {
    width: 160px;
    height: 280px;
    float: left;
    overflow: hidden;
  }

  .c_areas .ar_bom .item_sub {
    width: 108%;
    height: 100%;
    overflow-y: auto;
    background-color: #f5f5f5;
  }

  .c_areas .ar_bom .item_sub span {
    display: block;
    padding: 5px 0;
    text-indent: 10px;
    font-size: 14px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    cursor: pointer;
  }

  .c_areas .ar_btn {
    text-align: center;
    padding-top: 20px;
  }

  .c_areas .ar_btn span {
    display: inline-block;
    padding: 5px 30px;
    margin: 0 10px;
    cursor: pointer;
  }

  .c_areas .ar_btn span:first-child {
    color: #fff;
    background-color: #6f53ff;
  }

  .c_areas .ar_btn span:last-child {
    background-color: #eaeaea
  }
</style>



