import Vue from 'vue'
import zh_CN from 'vee-validate/dist/locale/zh_CN';
import VeeValidate, { Validator } from 'vee-validate';

/* 添加locale */
Validator.addLocale(zh_CN);

/* 注入到Vue实例 */
Vue.use(VeeValidate, {
  errorBagName: 'errors',
  delay: 0,
  locale: 'zh_CN',
  messages: null,
  strict: true
});

/* 自定义中文提示 */
const dictionary = {
  zh_CN: {
    messages: {
      required: ( field )=> "请输入" + field
    },
    attributes:{
      username:'账号',
      password:'密码',
      verifycode: '验证码',
      paypwd:'二级密码',
      realname: '真实姓名',
      idcard: '身份证',
      recoman: '推荐人',
      phone: '手机号',
      address:'地址',
      detailaddr: '详细地址',
      withdrawcredit:'提取积分',
      bankaccount: '银行账号',
      bankname:'开户行',
      banksubname:'支行名称',
      hascardname:'持卡人姓名',
      oldpwd:'旧密码',
      newpwd: '新密码',
      renewpwd: '确认新密码',
      suggest: '意见建议',
      qq: 'QQ',
      email: '邮箱'
    }
  }
};
Validator.updateDictionary(dictionary);

/* 自定义验证规则 */
Validator.extend('idcard', {
  messages: {
    zh_CN:field => '请输入合法'+field+'号',
  },
  validate: value => {
    return /^(^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$)|(^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])((\d{4})|\d{3}[Xx])$)$/.test(value)
  }
});

Validator.extend('phone', {
  messages: {
    zh_CN:field => '请输入合法'+field+'号',
  },
  validate: value => {
    return value.length == 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/.test(value)
  }
});
Validator.extend('username', {
  messages: {
    zh_CN:field => '请输入2-15位合法字符的'+field,
  },
  validate: value => {
    return /^[\u4E00-\u9FA5a-zA-Z0-9_]{2,15}$/.test(value)
  }
});







