<template>
  <!-- layout -->
  <div class="c_ucenter">
    <!-- 左边 -->
    <div class="uc_lt" :class="{init:initSidebar}">
      <div class="uc_logo"><img src="../../assets/uc_logo.png"></div>
      <div class="uc_text">个人中心 <i class="uc_arrow"></i></div>
      <div class="uc_info">
        <div class="uc_info_lt"><img src="../../assets/icon_user.png" alt=""></div>
        <div class="uc_info_rt">
          <h3>HI,用户名</h3>
          <i>购物激活</i>
        </div>
      </div>
      <div class="uc_nav">
        <ul>
          <li v-for="(item,index) in navs" :class="{active:index==activeId}" :id="'li'+index"><i :class="'icon'+index"></i><a :href="item.url" @click="switchActive(index)">{{item.name}}</a></li>
        </ul>
      </div>
    </div>
    <!-- 右边 -->
    <div class="uc_rt">
      <div class="uc_rt_top">
        <div class="btn_contr_sidebar" @click="onContrSidebar"></div>
        <div class="btn_sign_out"><i></i>退出</div>
      </div>
      <div class="uc_rt_main" :class="{init:initSidebar}">
        <transition name="fade" mode="out-in">
          <router-view></router-view>
        </transition>
      </div>
    </div>
  </div>
</template>
<script>
  /*import {getUserList} from '../../api/api'*/
  export default {
    data(){
      return {
        initSidebar: false,
        activeId:-1,
        navs: [
          {url:'#/user_center',name:'控制台'},
          {url:'#/my_wallet',name:'我的钱包'},
          {url:'#/my_reward',name:'我的奖励'},
          {url:'#/transfer_account',name:'宝合转账'},
          {url:'#/my_commend',name:'我的推荐'},
          {url:'#/apply_withdraw',name:'申请提现'},
          {url:'#/my_withdraw',name:'我的提现'},
          {url:'#/my_orders',name:'订单列表'},
          {url:'#/buy_products',name:'购买商品'},
          {url:'#/my_cart',name:'购物车'},
          {url:'#/consignee_address',name:'收货地址'},
          {url:'#/my_info',name:'我的信息'},
          {url:'#/modify_pwd',name:'密码修改'},
          {url:'#/modify_secpwd',name:'二级密码修改'},
          {url:'#/feed_back',name:'意见反馈'},
        ]
      }
    },
    mounted(){
      /*this.getUsers();*/
    },
    methods: {
      /*getUsers(){
       let para = {
       headers: {"x-token":"123123"},
       page: 1,
       name: ''
       };
       getUserList(para).then((res) => {
       console.log(res)
       });
       },
       signOut(){
       window.localStorage.removeItem("userName")
       window.location.reload();
       },*/
      onContrSidebar(){
        this.initSidebar = !this.initSidebar;
      },
      switchActive(id){
          this.activeId = id;
      }
    }
  }
</script>
<style lang="scss">
  @import "../../styles/common/lyt_uc.css";
</style>



