export default {
  /**
   根据父级显示子集集合
   **/
  children: function (pcode) {
    let city_list = [];
    for (let code in city_data) {
      let city = city_data[code];
      let parent_code = city['parent_code'];
      if (parent_code == pcode) {
        city_list.push(city);
      }
    }
    return city_list;
  },
  /**
   根据父级显示select标签的option标签
   **/
  childrenOption: function (pcode) {
    let option_data = this.children(pcode);
    let h = [];
    for (let i = 0; i < option_data.length; i++) {
      let _code = option_data[i]['code'];
      let _name = option_data[i]['name'];
      h.push({code:_code,name:_name});
    }
    return h;
  }
};



