<template>
  <!-- 申请提现 -->
  <div class="c_lyt_main">
    <h3 class="lm_title">申请提现</h3>
    <div class="lyt_uc_form">
      <div class="ucf_title">申请提现</div>
      <div class="ucf_main">
        <div class="ucf_tips">
          <strong>注意</strong>
          您账户中金积分余额：<i class="clr_rd">9999.00</i>,最低提现金额1000,每笔提现收取5%的手续费,每周只能提现一次。
        </div>
        <div class="ucf_form">
          <form @submit.prevent="applyWithdrawSubmit">
            <p class="fm_item">
              <label>提取积分：</label>
              <input type="text" name="withdrawcredit" v-model="withdrawcredit" v-validate="'required|numeric'">
              <span v-show="errors.has('withdrawcredit')" class="help is-danger">{{ errors.first('withdrawcredit')}}</span>
            </p>
            <p class="fm_item">
              <label>银行账号：</label>
              <input type="text" name="bankaccount" v-model="bankaccount" v-validate="'required'">
              <span v-show="errors.has('bankaccount')" class="help is-danger">{{ errors.first('bankaccount') }}</span>
            </p>
            <p class="fm_item">
              <label>开户行：</label>
              <input type="text" name="bankname" v-model="bankname" v-validate="'required'">
              <span v-show="errors.has('bankname')" class="help is-danger">{{ errors.first('bankname') }}</span>
            </p>
            <p class="fm_item">
              <label>支行名称：</label>
              <input type="text" name="banksubname" v-model="banksubname" v-validate="'required'">
              <span v-show="errors.has('banksubname')" class="help is-danger">{{ errors.first('banksubname') }}</span>
            </p>
            <p class="fm_item">
              <label>持卡人姓名：</label>
              <input type="text" name="hascardname" v-model="hascardname" v-validate="'required'">
              <span v-show="errors.has('hascardname')" class="help is-danger">{{ errors.first('hascardname') }}</span>
            </p>
            <p class="fm_btns">
              <button type="submit" class="btn_save">提现</button>
            </p>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data(){
      return {
        withdrawcredit: '',
        bankaccount: '',
        bankname: '',
        banksubname: '',
        hascardname: ''
      }
    },
    methods: {
      /* 申请提现提交函数 */
      applyWithdrawSubmit(){
        this.$validator.validateAll().then(() => {
          console.log('From Submitted!');
        }).catch(() => {
          console.log('Correct them errors!');
        });
      }
    }
  }
</script>
<style lang="scss">
  @import "../../styles/common/lyt_uc_main.css";
  @import "../../styles/common/lyt_uc_form.css";
</style>



